.rechat {
  height: 100%;
  justify-content: flex-end;
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
  overflow-y: auto;
  color: #efeff1;
  background-color: #141416;
}

.rechat ul {
  margin: 0px;
  padding: 0px;
  min-height: 0px;
  width: 100%;
  list-style: none;
}

.rechat li {
  display: list-item;
}
